"use client";
import React, { useEffect, useState } from "react";
import MyRecipeCardSkeleton from "./Skeleton/MyRecipeCardSkeleton";
import RecipeCard from "./RecipeCard";

const TopRecipes = () => {
  const [recipes, setRecipes] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    let isMounted = true;
    const loadTopRecipes = async () => {
      try {
        setIsLoading(true);
        const res = await fetch(
          `${process.env.NEXT_PUBLIC_API_URL}/top-recipes`
        );
        if (!res.ok) throw new Error("Failed to fetch top recipes");
        const data = await res.json();
        if (isMounted) setRecipes(data || []);
      } catch (err) {
        if (isMounted) setError(err.message || "Failed to load");
      } finally {
        if (isMounted) setIsLoading(false);
      }
    };
    loadTopRecipes();
    return () => {
      isMounted = false;
    };
  }, []);

  if (isLoading) {
    return (
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {[...Array(6)].map((_, i) => (
          <MyRecipeCardSkeleton key={i} />
        ))}
      </div>
    );
  }

  if (error) {
    return <p className="text-center text-sm text-red-600">{error}</p>;
  }

  return (
    <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
      {recipes.map((recipe) => (
        <RecipeCard key={recipe._id} recipe={recipe} />
      ))}
    </div>
  );
};

export default TopRecipes;
